import DistrictList from "@/components/DistrictList";

export default {
  name: "Districts",
  components: {DistrictList},
  data: () => ({
    districts: [],
    loading: true
  }),
  async mounted() {
    try {
      this.districts = await this.$store.dispatch('fetchDistricts')
      console.log(this.districts)
      this.loading = false
    } catch (e) {
      console.log(e)
    }
  }
}
